
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {loginRequest } from '../actions/user.action'
import Login from '../component/Login';
function handleError(error) {
    switch(error.message) {
        case "PASSWORD_INCORRECT": {
            return {
                message: "Sai mt khu. Vui lng th li hoc nhp vo Qun mt khu  t li mt khu."
            }
        }
        case "EMAIL_INCORRECT": {
            return {
                message: "Email khng ng. Vui lng th li"
            }
        }
    }
}
function mapStateToProps(state) {
   
    return {
        alert: handleError(state.alertReducer),  
        payload: state.authenReducer,
    };
}
function mapDispatchToProps(dispatch) {
    return {
        loginRequest: bindActionCreators(loginRequest,dispatch)
    };
}
export default connect(mapStateToProps, mapDispatchToProps)(Login);